import { Component, OnInit } from '@angular/core';
import { PostProvider } from '../../providers/post-provider';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController } from '@ionic/angular';
import { async } from 'q';
import { Storage } from '@ionic/storage';

@Component({
  selector: 'app-aceptar',
  templateUrl: './aceptar.page.html',
  styleUrls: ['./aceptar.page.scss'],
})
export class AceptarPage implements OnInit {

  
  customers: any = [];
  upsaldos: any = [];
  limit: number = 10;
  start: number = 0;
    name_customer: string = '';
  desc_customer: string = '';
  contact_customer: string = '';
  monto_customer: number;
   id: number;
  phone_number: string;
  username: string;
  name_destinatario;
  identificacion: string;
  user_id: any;
  saldo: number;
  login_id: any;
  anggota: any;

  constructor(
    private postPvdr: PostProvider,
    private router: Router,
    public toastController: ToastController,
    private actRoute: ActivatedRoute,
    private storage: Storage,
  ) { }

  

  ngOnInit() {
    this.actRoute.params.subscribe((data: any) => {
      this.id = data.id;
      this.name_customer = data.name;
      this.name_destinatario = data.destinatario;
     this.desc_customer = data.desc;
      this.contact_customer = data.contact;
      this.monto_customer = data.monto;
      this.login_id = data.login;
      this.saldo = data.saldo;
      this.identificacion = data.identificacion;
      this.username = data.username;
      console.log(data);
      });
  }

 

  ionViewWillEnter() {
    this.customers = [];
    this.upsaldos = [];     
    this.start = 0;
    //this.loadCustomer(this.login_id);
    this.loadSaldo();
    this.storage.get('session_storage').then((res) => {
      this.anggota = res;
     //this.login_id = this.anggota.login_id;
      this.user_id = this.anggota.user_id;
      this.saldo = this.anggota.saldo;
      this.phone_number = this.anggota.phone_number;
      this.username = this.anggota.username;
      this.identificacion = this.anggota.identificacion;
    });
  }

  

  async updateProses() {
    if (this.name_customer == ''){
      const toast = await this.toastController.create({
        message: 'Customer is required',
        duration: 2000
      });
      toast.present();
    } else if (this.desc_customer == ''){
      const toast = await this.toastController.create({
        message: this.username +  ', Cuentale a ' + this.name_customer + ' de tu transferencia' ,
        duration: 2000
      });
      toast.present();
    } else if (this.monto_customer == null){
      const toast = await this.toastController.create({
        message: 'Ingresa Un Monto Valido',
        duration: 2000
      });
      toast.present();
    } else if (this.monto_customer == 0){
      const toast = await this.toastController.create({
        message: 'Ingresa Un Monto Valido',
        duration: 2000
      });
      toast.present();
      } else {
        
      let body = {
    
        aksi: '1144168921',
        customer_id : this.id,
        name_customer : this.name_customer,
        name_destinatario : this.name_destinatario,
        desc_customer : this.desc_customer,
        contact_customer : this.contact_customer,
        monto_customer : this.monto_customer,
        saldo : this.saldo,
        login_id : this.login_id,
        user_id : this.user_id,
        username : this.username,
        identificacion : this.identificacion,
                  
      };
     // if ( this.saldo > this.monto_customer  ) { 
      this.postPvdr.postData(body, 'file_aksi.php').subscribe( async data => {
        var alertpesan = data.msg;          
        if (data.success) {
          this.UpinProces()
          this.proseslogout()
          this.monto_customer = null ;
         // this.router.navigate(['/customer']);
          const toast = await this.toastController.create({
            message: 'Data updated successful',
            duration: 2000
       
          });         
          
          //console.log(this.contact_customer);
          toast.present();
        } else {
          const toast = await this.toastController.create({
            message: alertpesan,
            duration: 2000
          });

     
      
        
        }
      
        
      });
    /*
    }else{
      const toast = await this.toastController.create({
        message: 'saldo',
        duration: 2000
      });
      toast.present();
    }
    */
  }

  
  }

  async proseslogout() {
    this.storage.clear();
    this.router.navigate(['/login']);
    const toast = await this.toastController.create({
      message: 'Logout successful',
      duration: 2000
     });
    toast.present();

  }


  doRefresh(event) {
    setTimeout(() => {
      this.ionViewWillEnter();
      event.target.complete();
    }, 500);
  }
  
  //aqui va la funcion para usar el salgo

  async RecargaProces(){
    const toast = await this.toastController.create({
      message: 'Revisa Tu Monto',
      duration: 2000
 
    });

    this.monto_customer = null;
    //this.router.navigate(['/customer']);
  }

  async UpinProces(){  
    let body = {    
      aksi: '87081758385',      
      monto_customer : this.monto_customer,
          saldo : this.saldo,  
      login_id : this.login_id,
      user_id: this.user_id,               
    };
    this.postPvdr.postData(body, 'file_aksi.php').subscribe( async data => {
      var alertpesan = data.msg;          
      if (data.success) {        
        //this.proseslogout()
        // this.router.navigate(['/customer']);
        const toast = await this.toastController.create({
          message: 'Por Tu Seguridad debes Iniciar de Nuevo ',
          duration: 2000
     
        });     
        
        //console.log(this.contact_customer);
        toast.present();
      } else {
        const toast = await this.toastController.create({
          message: alertpesan,
          duration: 2000
        });  
            }
    
      
    });

  }

  
  loadSaldo() {
  
    return new Promise(resolve => {
      let body = {
        aksi: 'getdata3',            
        saldo : this.saldo,
        user_id :this.user_id
       
      };
      //console.log(this.login_id);

      this.postPvdr.postData(body, 'file_aksi.php').subscribe(data => {
        if(data.result) {
          for(let upsaldo of data.result) {
           this.upsaldos.push(upsaldo)
          
           
        }
        }
        console.log(this.saldo);
        resolve(true);
      });
     
    });
  }


}
